                    <p>자청비와 문도령이 혼인하고 행복한 날들을 지내던 중 하루는 하늘의 법도를 어긴 자청비와 문도령을 죽이자는 소문이 돈다. 문도령은 가짜로 꾸민 궁의 행사에 참석하며 돌아오다가 노파의 독약주를 받아마시고 죽게 된다. 홀로 남은 자청비의 집으로 군사들이 들이닥치자, 자청비는 방문을 걸어잠궈두고 문도령이 방안에서 자고 있는 채 한다. 그녀는 꾀를 내어 무쇠로 만든 수제비와 무쇠로 만든 방석을 넌지시 보여주며 문도령의 힘이 장사라는 것을 과시하며 군사를 쫓아낸다. 이후 서천 꽃밭에서 피살이, 살살이 꽃을 꺾어와 죽은 문도령을 살려낸다. 이후 하늘의 왕에게 인정받은 자청비는 하늘의 곡식을 땅으로 가져와 농경의 신이 된다.</p>
